<template>
  <div>
    <div v-loading="loading" style="overflow: auto; height: 100vh">
      <el-row type="flex" justify="center" class="search-nav">
        <el-col :xs="20" :sm="18" :md="16" :lg="12" :xl="12">
          <el-card
            shadow="always"
            class="search-nav-el-card"
            :body-style="{ padding: 0 }"
          >
            <el-row type="flex" justify="space-around">
              <el-col :xs="8" :sm="8" :md="5" :lg="5" :xl="5">
                <el-cascader
                  v-model="search.from"
                  placeholder="出发地"
                  :options="options"
                  filterable
                  :props="search.pro"
                  :clearable="true"
                  :show-all-levels="false"
                  style="outline: none; border: none; box-shadow: none"
                ></el-cascader>
              </el-col>
              <el-col :xs="8" :sm="8" :md="5" :lg="5" :xl="5">
                <el-cascader
                  v-model="search.to"
                  placeholder="目的地"
                  :options="options"
                  filterable
                  :props="search.pro"
                  :clearable="true"
                  :show-all-levels="false"
                ></el-cascader>
              </el-col>
              <el-col :xs="8" :sm="8" :md="5" :lg="5" :xl="5">
                <el-date-picker
                  v-model="search.date"
                  type="date"
                  placeholder="选择日期"
                  editable
                  style="width: 100%"
                  :clearable="true"
                >
                </el-date-picker>
              </el-col>
              <el-col :xs="4" :sm="1" :md="1" :lg="1" :xl="1">
                <el-button
                  icon="el-icon-search"
                  circle
                  @click="searchFilter"
                ></el-button>
              </el-col>
            </el-row>
          </el-card>
        </el-col>
      </el-row>
      <el-row type="flex" justify="center" style="margin: 20px 0" v-if="isShow">
        <el-col :xs="20" :sm="18" :md="16" :lg="14" :xl="14">
          <el-card
            shadow="always"
            class="filter-el-card"
            :body-style="{ padding: '5px' }"
          >
            <div
              style="
                display: flex;
                justify-content: end;
                align-items: center;
                height: 100%;
              "
            >
              <el-select
                v-model="listQuery.price"
                placeholder="价格"
                style="width: 120px; margin: 0 10px"
                :clearable="true"
              >
                <el-option
                  v-for="(item, index) in priceOptions"
                  :key="index"
                  :label="item"
                  :value="item"
                >
                </el-option>
              </el-select>
              <el-select
                v-model="listQuery.time"
                placeholder="耗时"
                style="width: 120px; margin: 0 10px"
                :clearable="true"
              >
                <el-option
                  v-for="(item, index) in timeOptions"
                  :key="index"
                  :label="item"
                  :value="item"
                >
                </el-option>
              </el-select>
              <el-select
                v-model="listQuery.depart"
                placeholder="起飞时间"
                style="width: 140px; margin: 0 10px"
                :clearable="true"
              >
                <el-option
                  v-for="(item, index) in departOptions"
                  :key="index"
                  :label="item"
                  :value="item"
                >
                </el-option>
              </el-select>
              <el-select
                v-model="listQuery.arrive"
                placeholder="到达时间"
                style="width: 140px; margin: 0 10px"
                :clearable="true"
              >
                <el-option
                  v-for="(item, index) in arriveOptions"
                  :key="index"
                  :label="item"
                  :value="item"
                >
                </el-option>
              </el-select>
              <el-button
                :circle="true"
                slot="reference"
                class="el-icon-search"
                @click="handlerFilter"
              ></el-button>
            </div>
          </el-card>
        </el-col>
      </el-row>
      <el-row type="flex" justify="center" class="nav" v-if="isShow">
        <el-col
          :xs="20"
          :sm="18"
          :md="16"
          :lg="14"
          :xl="14"
          v-infinite-scroll="loadLimit"
        >
          <el-card
            shadow="always"
            class="info-el-card"
            :body-style="{ padding: 0 }"
            v-for="(plane, index) in planesPart"
            :key="index"
          >
            <el-row
              type="flex"
              justify="space-between"
              align="middle"
              style="height: 120px"
            >
              <el-col :xs="5" :sm="4" :md="4" :lg="4" :xl="4">
                <div class="row">
                  <el-image
                    :src="require('@/assets/png/' + plane.str + '.png')"
                    style="
                      margin-right: 10px;
                      max-height: 50px;
                      max-width: 50px;
                    "
                  ></el-image>
                  <div class="col">
                    <h3>{{ planeDir[plane.str] }}</h3>
                    <div>EU2000</div>
                  </div>
                </div>
              </el-col>
              <el-col :xs="10" :sm="8" :md="10" :lg="10" :xl="10">
                <el-popover
                  placement="top"
                  title="航班信息"
                  width="300"
                  trigger="hover"
                >
                  <el-divider></el-divider>
                  <div>
                    <el-timeline>
                      <el-timeline-item
                        timestamp="4/12 08:00 北京首都机场"
                        placement="top"
                        color="#54c8f4"
                      >
                        <el-card>飞行时间 1 小时</el-card>
                      </el-timeline-item>
                      <el-timeline-item
                        timestamp="4/13 10:00 上海虹桥"
                        placement="top"
                        color="#54c8f4"
                      >
                      </el-timeline-item>
                    </el-timeline>
                  </div>
                  <div class="row" slot="reference">
                    <div class="col">
                      <h3>北京首都</h3>
                      <div>08:00</div>
                    </div>
                    <el-image
                      :src="require('@/assets/arrow.png')"
                      style="margin: 0 10px; width: 40px; height: 30px"
                    ></el-image>
                    <div class="col">
                      <h3>上海虹桥</h3>
                      <div>10:00</div>
                    </div>
                  </div>
                </el-popover>
              </el-col>
              <el-col :xs="5" :sm="3" :md="3" :lg="3" :xl="3">
                <h3 class="price" style="color: #409eff; font-size: 25px">
                  ￥1234
                </h3>
              </el-col>
              <el-col :xs="4" :sm="3" :md="3" :lg="3" :xl="3">
                <el-button
                  type="primary"
                  size="medium"
                  class="el-icon-shopping-cart-2"
                  @click="order(plane)"
                  >订购</el-button
                >
              </el-col>
            </el-row>
          </el-card>
        </el-col>
      </el-row>
      <div
        style="
          display: flex;
          justify-content: center;
          align-items: center;
          position: fixed;
          left: 50%;
          bottom: 20px;
        "
      >
        <div
          style="height: 40px; width: 40px"
          v-loading="isInfiniteLoading"
        ></div>
      </div>
      <div class="loading" v-if="cardLoading"></div>
    </div>

    <el-dialog
      title="订单"
      :visible.sync="dialog"
      append-to-body
      fullscreen
      width="700px"
      customClass="customWidth"
      @open="initOrder"
    >
      <div class="col">
        <el-steps
          :active="active"
          style="width: 900px; background: #fff"
          align-center
        >
          <el-step title="乘机信息"></el-step>
          <el-step title="增值服务"></el-step>
          <el-step title="支付"></el-step>
          <el-step title="完成"></el-step>
        </el-steps>
        <el-divider></el-divider>
        <div style="width: 100%; height: 700px" v-if="active == 0">
          <div class="row">
            <div class="col">
              <el-card
                style="width: 800px; height: 550px; overflow-y: auto"
                header="乘机人"
              >
                <div class="col">
                  <el-card
                    v-for="(passenger, index) in orderInfo.passengers"
                    :key="index"
                    style="
                      width: 100%;
                      height: 150px;
                      margin-bottom: 20px;
                      position: relative;
                    "
                    body-style="padding: 0"
                    shadow="hover"
                  >
                    <div
                      class="row"
                      style="justify-content: start; align-items: start"
                    >
                      <h1 style="margin: 10px 30px">{{ index }}</h1>
                      <div
                        style="
                          width: 2px;
                          height: 140px;
                          background: #eee;
                          margin: 5px 10px;
                        "
                      ></div>
                      <el-form
                        :model="passenger"
                        label-width="100px"
                        :rules="rules"
                        ref="passengerForm"
                        style="margin-top: 10px"
                      >
                        <el-form-item label="姓名" size="mini" prop="name">
                          <el-input
                            size="mini"
                            placeholder="乘机人姓名"
                            v-model="passenger.name"
                          ></el-input>
                        </el-form-item>
                        <el-form-item label="身份证" size="mini" prop="id">
                          <el-input
                            size="mini"
                            placeholder="乘机人身份证号"
                            v-model="passenger.id"
                          ></el-input>
                        </el-form-item>
                        <el-form-item label="联系电话" size="mini" prop="phone">
                          <el-input
                            size="mini"
                            placeholder="乘机人电话号码"
                            v-model="passenger.phone"
                          ></el-input>
                        </el-form-item>
                      </el-form>
                      <el-button
                        class="el-icon-close"
                        type="text"
                        style="
                          font-size: 30px;
                          color: #409eff;
                          position: absolute;
                          right: 10px;
                          top: 0;
                        "
                        @click="deletePassenger(index)"
                      ></el-button>
                    </div>
                  </el-card>
                  <el-button
                    class="el-icon-circle-plus-outline"
                    type="text"
                    style="font-size: 20px; color: #409eff"
                    @click="addPassenger"
                    >增加乘机人</el-button
                  >
                </div>
              </el-card>
              <el-card
                style="width: 800px; height: 120px; margin-top: 20px"
                header="联系人"
              >
                <div class="col">
                  <el-form
                    :model="orderInfo.contact"
                    label-width="100px"
                    ref="contactForm"
                    :rules="contactRules"
                  >
                    <el-form-item label="联系电话" size="mini" prop="phone">
                      <el-input
                        size="mini"
                        placeholder="联系人电话号码"
                        style="width: 300px; margin-right: 20px"
                        v-model="orderInfo.contact.phone"
                      ></el-input>
                      <div class="el-icon-warning" style="color: #7c7c7c">
                        订单信息将发送到此手机号
                      </div>
                    </el-form-item>
                  </el-form>
                </div>
              </el-card>
            </div>
            <el-card
              style="width: 400px; height: 700px; margin-left: 20px"
              header="信息"
            >
              <div class="col">
                <h2>周日 05-23 北京-上海</h2>
                <h2>东方航空</h2>
                <el-divider></el-divider>
                <div
                  v-for="i in 3"
                  :key="i"
                  style="width: 100%; margin-bottom: 10px"
                >
                  <div class="col">
                    <div
                      class="row"
                      style="width: 100%; justify-content: space-between"
                    >
                      <div>经济舱</div>
                      <div>500￥</div>
                    </div>
                  </div>
                  <div class="col">
                    <div
                      class="row"
                      style="width: 100%; justify-content: space-between"
                    >
                      <div>机场建设费</div>
                      <div>200￥</div>
                    </div>
                  </div>
                  <div class="col">
                    <div
                      class="row"
                      style="width: 100%; justify-content: space-between"
                    >
                      <div>燃油费</div>
                      <div>200￥</div>
                    </div>
                  </div>
                </div>
                <el-divider></el-divider>
                <h1 style="width: 100%; text-align: right; font-size: 20px">
                  1220￥
                </h1>
              </div>
            </el-card>
          </div>
        </div>
        <div style="width: 100%; height: 700px" v-if="active == 1">
          <el-card style="width: 100%; height: 700px" header="增值服务">
            <el-empty description="暂无可用增值服务"></el-empty>
          </el-card>
        </div>
        <div style="width: 100%; height: 700px" v-if="active == 2">
          <el-card style="width: 100%; height: 700px" header="支付">
            <el-card
              style="width: 100%; height: 200px; background-color: #78dce8"
              header="订单详情"
            >
            </el-card
            ><el-divider></el-divider>
            <div style="margin-bottom: 20px; font-size: 18px">支付方式</div>
            <el-tabs>
              <el-tab-pane label="支付宝"></el-tab-pane>
              <el-tab-pane label="微信"></el-tab-pane>
            </el-tabs>
          </el-card>
        </div>
        <div style="width: 100%; height: 700px" v-if="active == 3">
          <el-result
            icon="success"
            title="支付成功"
            subTitle="结束操作"
            style="margin-top: 100px"
          >
            <template slot="extra">
              <el-button type="primary" size="medium">关闭页面</el-button>
            </template>
          </el-result>
        </div>
        <div class="row" style="margin-top: 10px">
          <el-button
            type="primary"
            @click="last"
            v-if="active > 0"
            style="width: 300px; margin-right: 20px"
            >上一步</el-button
          >
          <el-button
            type="primary"
            @click="next"
            v-if="active < 3"
            style="width: 300px"
            >下一步</el-button
          >
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
