export default {
  name: "YTrain",
  data() {
    return {
      limit: 10,
      loading: true,
      cardLoading: false,
      isInfiniteLoading: false,
      options: constData.options,
      isShow: false,
      planeDir: data.planeInfo,
      planes: planes,
      planesPart: "",
      search: {
        from: "",
        to: "",
        date: "",
        pro: {
          expandTrigger: "hover",
        },
      },
      timeOptions: ["耗时升序", "耗时降序"],
      priceOptions: ["价格升序", "价格降序"],
      arriveOptions: ["到达时间升序", "到达时间降序"],
      departOptions: ["出发时间升序", "出发时间降序"],
      listQuery: {
        time: "",
        price: "",
        arrive: "",
        depart: "",
      },
      rules: {
        name: [
          { required: true, message: "请输入姓名", trigger: "blur" },
          {
            min: 2,
            max: 10,
            message: "长度在 2 到 10 个字符",
            trigger: "blur",
          },
        ],
        id: [
          { required: true, message: "请输入身份证号", trigger: "blur" },
          { min: 18, max: 18, message: "长度为 18 个字符", trigger: "blur" },
        ],
        phone: [
          { required: true, message: "请输入手机号", trigger: "blur" },
          { min: 11, max: 11, message: "长度为 11 个字符", trigger: "blur" },
        ],
      },
      contactRules: {
        phone: [
          { required: true, message: "请输入手机号", trigger: "blur" },
          { min: 11, max: 11, message: "长度为 11 个字符", trigger: "blur" },
        ],
      },
      dialog: false,
      active: 0,
      orderInfo: {
        passengers: [
          {
            name: "",
            id: "",
            phone: "",
          },
        ],
        contact: {
          phone: "",
        },
      },
    };
  },
  methods: {
    ...mapMutations("login", {
      changeLoginDialog: "changeLoginDialog",
    }),
    loadLimit() {
      console.log("loadLimit");
      this.limit += 5;
      this.planesPart = this.planes.slice(0, this.limit);
      this.isInfiniteLoading = true;
      setTimeout(() => {
        this.isInfiniteLoading = false;
      }, 500);
    },
    searchFilter() {
      this.cardLoading = true;
      this.isShow = false;
      setTimeout(() => {
        this.cardLoading = false;
        this.isShow = true;
      }, 800);
    },
    handlerFilter() {
      console.log(this.listQuery);
    },
    order(row) {
      if (this.$store.state.login.loginState == false) {
        this.changeLoginDialog();
        this.$message({
          message: "请先登录",
          type: "warning",
        });
      } else {
        console.log(row);
        this.dialog = true;
      }
    },
    next() {
      this.active++;
      if (this.active < 4) {
        if (this.active == 0) {
          let judge = true;
          this.$refs["passengerForm"].forEach((item) => {
            item.validate((valid) => {
              if (!valid) {
                judge = false;
              }
            });
          });
          if (!judge) {
            this.$message({
              message: "请填写正确的乘客信息",
              type: "warning",
            });
            return;
          } else {
            this.$refs.contactForm.validate((valid) => {
              if (valid) {
                // this.active++;
              } else {
                this.$message({
                  message: "请填写联系人信息",
                  type: "warning",
                });
              }
            });
          }
        } else {
          // this.active++;
        }
      }
    },
    last() {
      if (this.active > 0) {
        this.active--;
      }
    },
    initOrder() {
      this.orderInfo = {
        passengers: [
          {
            name: "",
            id: "",
            phone: "",
          },
        ],
        contact: {
          phone: "",
        },
      };
    },
    addPassenger() {
      let passenger = {
        name: "",
        id: "",
        phone: "",
      };
      this.orderInfo.passengers.push(passenger);
    },
    deletePassenger(index) {
      this.orderInfo.passengers.splice(index, 1);
    },
  },
  activated() {
    this.planesPart = this.planes.slice(0, this.limit);
    setTimeout(() => {
      this.loading = false;
    }, 500);
  },
  deactivated() {
    this.loading = true;
